import Vue from 'vue'

export const slideShow = Vue.extend({
  data() {
    return {
      slideCount: 0,
      slideLength: 0,
      slideClass: [],
      slideCurrent: 0,
      showLock: false,
    }
  },
  methods: {
    slideInit(length, startIndex = 0) {
      this.slideLength = length;
      this.slideCurrent = startIndex;
      for (let i = 0; i < length; i++) {
        let classArray = i === this.slideCurrent ? ['active'] : [];
        this.$set(this.slideClass, i, classArray);
      }
    },
    slideTo(index) {
      this.slideCount = index;
      this.showing(index);
    },
    prevSlide(infinity = false) {
      const max = this.slideLength - 1;
      if (infinity && this.slideCount >= 0) {
        this.slideCount = max;
      } else if (!infinity && this.slideCount >= 0) {
        this.slideCount = 0;
      } else {
        this.slideCount--;
      }
      this.showing(this.slideCount, infinity);
    },
    nextSlide(infinity = false) {
      const max = this.slideLength - 1;
      if (infinity && this.slideCount >= max) {
        this.slideCount = 0;
      } else if (!infinity && this.slideCount >= max) {
        this.slideCount = max;
      } else {
        this.slideCount++;
      }
      this.showing(this.slideCount, infinity);
    },
    autoPlay(index, speed = 4000, destroyed = false) {
      // 重複呼叫時清掉計時器, destroyed也要清除
      let start;
      clearInterval(start);
      if (destroyed) return;

      const vm = this;

      // 開始播放，預設每四秒輪播一次
      start = setInterval(function () {
        vm.nextSlide(true);
      }, speed);
    },
    showing(index, infinity = false) {
      if(this.showLock) return;

      this.showLock = true;
      const vm = this;
      let prev = vm.slideCurrent;

      console.log(index, prev);

      if (index === prev ) {
        this.showLock = false;
        return;
      }

      let start1, start2;
      const className1 = index > prev || infinity ? "carousel-item-next" : "carousel-item-prev",
            className2 = index > prev || infinity ? "carousel-item-left" : "carousel-item-right";

      clearTimeout(start1);
      clearTimeout(start2);

      vm.slideCurrent = index;

      vm.$set(vm.slideClass, index, ['active']);
      vm.$set(vm.slideClass, index, ['active', className1]);

      start1 = setTimeout(function () {
        vm.$set(vm.slideClass, index, ['active']);
        vm.$set(vm.slideClass, prev, ['active', className2]);
      }, 10);

      start2 = setTimeout(function () {
        vm.$set(vm.slideClass, index, ['active']);
        vm.$set(vm.slideClass, prev, ['active']);
        vm.$set(vm.slideClass, prev, []);
        vm.showLock = false;
      }, 600);
    }
  },
  destroyed() {
    this.autoPlay(null, null, true);
  }
});
